'use client';

import { createContext, useEffect, useState } from 'react';

// Create the context
export const DataContext = createContext<any>(null);

export function DataProvider({ children }: { children: React.ReactNode }) {
  const [portfolioData, setPortfolioData] = useState({
    personalInfo: null,
    projects: [],
    experiences: [],
    skills: [],
  });

  // üîπ Load cached data or fetch fresh on mount
  useEffect(() => {
    const storedData = localStorage.getItem('portfolioData');

    if (storedData) {
      console.log('üì¶ Loaded portfolioData from localStorage');
      setPortfolioData(JSON.parse(storedData));
    } else {
      console.log('üöÄ Fetching portfolioData from API');
      loadFromAPI();
    }
  }, []);

  // üîπ Fetch everything from API
  async function loadFromAPI() {
    try {
      const [personalInfo, projects, experiences, skills] = await Promise.all([
        fetch('/api/personal-info', { cache: 'no-store' }).then((r) => r.json()),
        fetch('/api/projects', { cache: 'no-store' }).then((r) => r.json()),
        fetch('/api/experiences', { cache: 'no-store' }).then((r) => r.json()),
        fetch('/api/skills', { cache: 'no-store' }).then((r) => r.json()),
      ]);

      const fetched = {
        personalInfo: personalInfo ?? {},
        projects: Array.isArray(projects) ? projects : [],
        experiences: Array.isArray(experiences) ? experiences : [],
        skills: Array.isArray(skills) ? skills : [],
      };

      setPortfolioData(fetched);
      localStorage.setItem('portfolioData', JSON.stringify(fetched));
      console.log('‚úÖ Portfolio data loaded and cached');
    } catch (err) {
      console.error('‚ùå Failed to load portfolio data:', err);
    }
  }

  // üîπ Keep localStorage + server in sync on every change
  useEffect(() => {
    if (!portfolioData) return;

    // Update local storage immediately
    localStorage.setItem('portfolioData', JSON.stringify(portfolioData));

    // Push updates to the server asynchronously
    (async () => {
      try {
        const res = await fetch('/api/sync-portfolio', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ data: portfolioData }),
        });

        if (!res.ok) {
          console.error('‚ùå Server sync failed');
        } else {
          console.log('‚úÖ Portfolio sync successful');
        }
      } catch (err) {
        console.error('‚ùå Error during server sync:', err);
      }
    })();
  }, [portfolioData]);

  return (
    <DataContext.Provider value={{ portfolioData, setPortfolioData }}>
      {children}
    </DataContext.Provider>
  );
}
